<template>
  <section class="section">
    <h1 class="title">RetroAchievements</h1>
    <a href="https://cors-anywhere.herokuapp.com/corsdemo" target="_blank" rel="noopener noreferrer">Allow to see comments</a>

    <b-field grouped>
      <b-field label="Username" expanded>
        <b-input v-model="username"></b-input>
      </b-field>
      <b-field label="API Key" expanded>
        <b-input type="password" v-model="apiKey" password-reveal></b-input>
      </b-field>
      <b-field label="‎">
        <button class="button is-dark" @click="onLogin">Login</button>
      </b-field>
    </b-field>

    <div class="columns" v-if="userSummary.ID">
      <div class="column is-one-quarter">
        <div class="card">
          <div class="card-image p-5">
            <b-image alt="Player Picture" :src="'https://retroachievements.org' + userSummary.UserPic" :responsive="true" :rounded="false"></b-image>
          </div>
          <div class="card-content">
            <div class="media">
              <div class="media-content">
                <p class="title is-4">{{ username }}</p>
                <p class="subtitle is-6">
                  <em>Member Since: {{ userSummary.MemberSince }}</em>
                </p>
              </div>
            </div>

            <div class="content">
              <div><strong>Rank: </strong>{{ userSummary.Rank }}</div>
              <div><strong>Points: </strong>{{ userSummary.Points }}</div>
              <div>
                <strong>True Points: </strong>{{ userSummary.TotalTruePoints }}
              </div>
              <div>
                <strong>Total Awards: </strong>{{ Object.keys(userSummary.Awarded).length }}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="column">
        <b-autocomplete open-on-focus v-model="gameName" :data="filteredRecentlyPlayed" field="Title" placeholder="Search in your recently played games" icon="magnify" @select="onSelectGame">
          <template slot-scope="props">
            {{ props.option.Title }}
          </template>
        </b-autocomplete>

        <hr />
        <div class="card" v-if="selectedGame.ID">
          <div class="card-image">
            <div class="columns has-text-centered pt-5">
              <div class="column">
                <b-image alt="Player Picture" :src="
                  'https://retroachievements.org' + selectedGame.ImageBoxArt
                " :responsive="false"></b-image>
              </div>
              <div class="column">
                <b-image alt="Player Picture" :src="
                  'https://retroachievements.org' + selectedGame.ImageIngame
                " :responsive="false"></b-image>
              </div>
              <div class="column">
                <b-image alt="Player Picture" :src="
                  'https://retroachievements.org' + selectedGame.ImageTitle
                " :responsive="false"></b-image>
              </div>
            </div>
          </div>
          <div class="card-content">
            <div class="media">
              <div class="media-content">
                <b-tooltip type="is-dark">
                  <p class="title is-4">{{ selectedGame.Title }}</p>
                  <template v-slot:content>
                    ID: {{ selectedGame.ID }}
                  </template>
                </b-tooltip>
                <p class="subtitle is-6">
                  <em>Release Data: {{ selectedGame.Released }}</em>
                  (<a @click="getGameComments(selectedGame.ID)">comments</a>)
                </p>
              </div>
            </div>

            <div class="content">
              <div class="columns">
                <div class="column">
                  <div>
                    <strong>Console Name: </strong>{{ selectedGame.ConsoleName }}
                  </div>
                  <div>
                    <strong>Developer: </strong>{{ selectedGame.Developer }}
                  </div>
                  <!-- <div>
                    <strong>Publisher: </strong>{{ selectedGame.Publisher }}
                  </div> -->
                  <div><strong>Genre: </strong>{{ selectedGame.Genre }}</div>
                </div>
                <div class="column">
                  <div>
                    <strong>Total Achievements: </strong>{{ selectedGame.NumAchievements }}
                  </div>
                  <div>
                    <strong>Total Casual Players: </strong>{{ selectedGame.NumDistinctPlayersCasual }}
                  </div>
                  <div>
                    <strong>Total Hardcore Players: </strong>{{ selectedGame.NumDistinctPlayersHardcore }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <b-field grouped group-multiline position="is-centered" v-if="selectedGame.Achievements">
      <p class="control">
        <b-radio v-model="achievementsFilter" :native-value="0" type="is-dark">
          <span>All</span>
        </b-radio>
      </p>
      <p class="control">
        <b-radio v-model="achievementsFilter" :native-value="1" type="is-dark">
          <span>I have</span>
        </b-radio>
      </p>
      <p class="control">
        <b-radio v-model="achievementsFilter" :native-value="2" type="is-dark">
          <span>I don't have</span>
        </b-radio>
      </p>
      <b-input placeholder="Custom Filter (Contains)" type="search" icon="magnify-plus" v-model="achievementsFilterStringContains"></b-input>
      <b-input placeholder="Custom Filter (Not Contains)" type="search" icon="magnify-minus" v-model="achievementsFilterStringNotContains"></b-input>
      <p class="control">
        <b-tag rounded type="is-dark" size="is-medium">Total: {{ filteredAchievements.length }}</b-tag>
      </p>
    </b-field>

    <b-field grouped group-multiline position="is-centered" v-if="selectedGame.Achievements">
      <p class="control">
        <b-radio @input="onAchievementsOrder" v-model="achievementsOrder" :native-value="0" type="is-dark">
          <span>Clear Ordering</span>
        </b-radio>
      </p>
      <p class="control">
        <b-radio @input="onAchievementsOrder" v-model="achievementsOrder" :native-value="1" type="is-info">
          <span>Order by Points</span>
        </b-radio>
      </p>
      <p class="control">
        <b-radio @input="onAchievementsOrder" v-model="achievementsOrder" :native-value="2" type="is-primary">
          <span>Order by Difficult</span>
        </b-radio>
      </p>
    </b-field>

    <b-field grouped group-multiline position="is-centered" v-if="selectedGame.Achievements">
      <p class="control">
        <b-button type="is-dark" icon-left="refresh" rounded @click="onRefreshAchievements">
          Refresh Achievements
        </b-button>
      </p>
    </b-field>

    <div class="columns is-multiline" v-if="selectedGame.Achievements">
      <div class="column is-one-quarter" v-for="(achievement, index) in filteredAchievements" :key="index">
        <div class="card">
          <div class="card-image"></div>
          <div class="card-content">
            <div class="columns is-mobile">
              <div class="column is-4 has-text-centered">
                <b-image alt="Player Picture" :src="
                  'https://s3-eu-west-1.amazonaws.com/i.retroachievements.org/Badge/' +
                  achievement.BadgeName +
                  (achievement.DateEarned ? '' : '_lock') +
                  '.png'
                " :responsive="false" :rounded="true"></b-image>
              </div>

              <div class="column">
                <span class="title is-5">{{ achievement.Title }}</span>
              </div>
            </div>
            <div class="content">
              <b-field grouped group-multiline>
                <div class="control">
                  <b-taglist attached>
                    <b-tag type="is-dark is-light">Points</b-tag>
                    <b-tag type="is-info">{{ achievement.Points }}</b-tag>
                  </b-taglist>
                </div>
                <div class="control">
                  <b-taglist attached>
                    <b-tag type="is-dark is-light">Difficult</b-tag>
                    <b-tag type="is-primary">{{ achievement.Difficult }}%</b-tag>
                  </b-taglist>
                </div>
              </b-field>
              {{ achievement.Description }}

              (<a @click="getAchievementsComments(achievement.ID)">comments</a>)
            </div>
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="isCommentsModalActive">
      <div class="card">
        <div class="card-image"></div>
        <div class="card-content">
          <b-table class="is-size-7" :data="comments" :striped="true" :narrowed="true">
            <b-table-column width="120" field="date" label="Date" v-slot="props"><em>{{ props.row.date }}</em></b-table-column>
            <b-table-column field="user" label="User" v-slot="props"><strong>{{ props.row.user }}</strong></b-table-column>
            <b-table-column field="description" label="Comment" v-slot="props">{{ props.row.description }}</b-table-column>
          </b-table>
        </div>

        <b-field grouped position="is-right">
          <p class="control mr-5">
            <b-button type="is-dark" size="is-small" @click="isCommentsModalActive = false">Close</b-button>
          </p>
        </b-field>
        <br />
      </div>
    </b-modal>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "RetroAchievements",
  data() {
    return {
      username: "",
      apiKey: "",
      userSummary: {},
      gameName: "",
      selectedGame: {},
      achievementsOrder: 0,
      achievementsFilter: 0,
      achievementsFilterStringContains: "",
      achievementsFilterStringNotContains: "",
      comments: [],
      isCommentsModalActive: false,
    };
  },
  methods: {
    async fetchData(query) {
      let loadingComponent = this.$buefy.loading.open();
      let result = false;
      try {
        const response = await axios.get(
          `https://retroachievements.org/API/${query}`
        );
        result = response.data;
        if (typeof result === "string" || result instanceof String) {
          this.$buefy.toast.open({
            message: `Erro: ${result}`,
            type: "is-danger",
          });
        }
      } catch (error) {
        this.$buefy.toast.open({
          message: error,
          type: "is-danger",
        });
        console.error(error);
      }
      loadingComponent.close();

      return result;
    },
    async onLogin() {
      this.userSummary = await this.fetchData(
        `API_GetUserSummary.php?z=${this.username}&y=${this.apiKey}&u=${this.username}&g=50&a=100`
      );

      if (this.userSummary) {
        localStorage.setItem("RetroAchievements.Username", this.username);
        localStorage.setItem("RetroAchievements.ApiKey", this.apiKey);

        for (const key in this.userSummary.Awarded) {
          if (Object.hasOwnProperty.call(this.userSummary.Awarded, key)) {
            const element = this.userSummary.Awarded[key];
            let total = parseInt(element.NumPossibleAchievements);
            let achieved = parseInt(element.NumAchievedHardcore);
            if (achieved && achieved === total) {
              console.log(`Site Awards: ${key}`);
            }
          }
        }
      }
    },
    async onSelectGame(game) {
      if (game) {
        this.selectedGame = await this.fetchData(
          `API_GetGameInfoAndUserProgress.php?z=${this.username}&y=${this.apiKey}&u=${this.username}&g=${game.GameID}`
        );

        if (this.selectedGame) {
          localStorage.setItem(
            "RetroAchievements.SelectedGame",
            JSON.stringify(this.selectedGame)
          );
        }

        this.filteredAchievements.sort((a, b) => {
          return parseInt(a.DisplayOrder) - parseInt(b.DisplayOrder);
        });
      }
    },
    async onRefreshAchievements() {
      await this.onSelectGame({ GameID: this.selectedGame.ID });
    },
    onAchievementsOrder() {
      switch (this.achievementsOrder) {
        case 0:
          this.filteredAchievements.sort((a, b) => {
            return parseInt(a.DisplayOrder) - parseInt(b.DisplayOrder);
          });
          break;

        case 1:
          this.filteredAchievements.sort((a, b) => {
            return parseInt(b.Points) - parseInt(a.Points);
          });
          break;

        case 2:
          this.filteredAchievements.sort((a, b) => {
            return parseInt(b.Difficult) - parseInt(a.Difficult);
          });
          break;
      }
    },
    async fetchHtml(path) {
      let loadingComponent = this.$buefy.loading.open();
      let htmlDoc = {};
      try {
        const response = await axios.get(
          `https://cors-anywhere.herokuapp.com/https://retroachievements.org/${path}`
        );
        let html = response.data;
        let parser = new DOMParser();
        htmlDoc = parser.parseFromString(html, "text/html");
      } catch (error) {
        this.$buefy.toast.open({
          message: error,
          type: "is-danger",
        });
        console.error(error);
      }
      loadingComponent.close();

      return htmlDoc;
    },
    async parseComments(htmlDoc) {
      this.comments = [];
      const feedTable = htmlDoc.querySelector("#feed");
      const rows = feedTable.rows;
      for (const key in rows) {
          const tr = rows[key];
          if (tr.className && tr.className.indexOf('system') === - 1) { // filter out system messages
              const tds = tr.querySelectorAll('td');
              let date = new Date(tds[1].querySelectorAll('span')[1].textContent.trim());
              if ((date.toString() === 'Invalid Date')) {
                  date = new Date(tds[1].querySelectorAll('span')[2].textContent.trim());
              }
              date = date.toDateString();
              const comment = tds[1].querySelectorAll('div')[1].textContent.trim();
              const obj = { date: date,user: '------',description: comment };
              this.comments.push(obj);
          }
      }

      if (this.comments.length) {
        this.isCommentsModalActive = true;
      } else {
        this.$buefy.dialog.alert("No comments for this achievement!");
      }
    },
    async getAchievementsComments(id) {
      let htmlDoc = await this.fetchHtml(`achievement/${id}`);
      this.parseComments(htmlDoc);
    },
    async getGameComments(id) {
      let htmlDoc = await this.fetchHtml(`game/${id}`);
      this.parseComments(htmlDoc);
    },
  },
  computed: {
    filteredRecentlyPlayed() {
      return this.userSummary.RecentlyPlayed.filter((option) => {
        return (
          option.Title.toString()
            .toLowerCase()
            .indexOf(this.gameName.toLowerCase()) >= 0
        );
      });
    },
    filteredAchievements() {
      let achievements = Object.values(this.selectedGame.Achievements);
      achievements = achievements.map((currentAchievement) => {
        currentAchievement.Difficult =
          100 -
          Math.round(
            (currentAchievement.NumAwardedHardcore /
              this.selectedGame.NumDistinctPlayersHardcore) *
            100
          );
        return currentAchievement;
      });

      let filtered = achievements.filter((option) => {
        let canReturn = false;
        switch (this.achievementsFilter) {
          case 0:
            canReturn = true;
            break;
          case 1:
            if (option.DateEarned) {
              canReturn = true;
            }
            break;
          case 2:
            if (!option.DateEarned) {
              canReturn = true;
            }
            break;

          default:
            break;
        }

        if (this.achievementsFilterStringContains !== "" && canReturn) {
          let regex = RegExp(
            this.achievementsFilterStringContains.toString().toLowerCase()
          );
          if (
            regex.test(option.Description.toString().toLowerCase()) ||
            regex.test(option.Title.toString().toLowerCase())
          ) {
            canReturn = true;
          } else {
            canReturn = false;
          }
        }
        if (this.achievementsFilterStringNotContains !== "" && canReturn) {
          let regex = RegExp(
            this.achievementsFilterStringNotContains.toString().toLowerCase()
          );
          if (
            regex.test(option.Description.toString().toLowerCase()) ||
            regex.test(option.Title.toString().toLowerCase())
          ) {
            canReturn = false;
          } else {
            canReturn = true;
          }
        }

        if (canReturn) {
          return option;
        }
      });

      return filtered;
    },
  },
  async created() {
    let username = localStorage.getItem("RetroAchievements.Username");
    let apiKey = localStorage.getItem("RetroAchievements.ApiKey");
    if (username && apiKey) {
      this.username = username;
      this.apiKey = apiKey;
      await this.onLogin();
      let selectedGame = localStorage.getItem("RetroAchievements.SelectedGame");
      if (selectedGame) {
        this.selectedGame = JSON.parse(selectedGame);
        this.filteredAchievements.sort((a, b) => {
          return parseInt(a.DisplayOrder) - parseInt(b.DisplayOrder);
        });
      }
    }
  },
};
</script>
